<template>
    <v-container fluid>
        <v-row class="col-12">
            <v-col class="col-12">
                <v-card class="ma-3" :elevation="5">
                    <v-card-title>
                        Informacje o zgoszeniu: {{ ticket.token }}

                        <v-spacer></v-spacer>

                        <ticket-management :ticket="ticket"></ticket-management>
                    </v-card-title>
                </v-card>
            </v-col>

            <v-col class="col-12 col-sm-12 col-md-6 col-lg-4 col-xl-4">
                <ticket :ticket="ticket" :loading="loading"></ticket>
                <device :device="ticket.device" :loading="loading"></device>
                <client :client="ticket.client" :loading="loading"></client>
            </v-col>

            <v-col class="col-12 col-sm-12 col-md-6 col-lg-8 col-xl-8">
                <timeline v-if="notesLength" :loading="notesLoading" title="Notatki" :itemsCount="notesLength">

                    <template v-slot:createForm>
                        <notes-create :ticket_id="ticket.id"></notes-create>
                    </template>

                    <note v-for="note in notes.data" :key="note.id"
                          :note="note"></note>

                    <template v-slot:pagination>
                        <v-pagination
                                v-model="page"
                                :length="notes.last_page"
                                total-visible="7"
                                circle
                        ></v-pagination>
                    </template>
                </timeline>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import Device from "../Device/Device";
    import Timeline from "../Timelines/Timeline";
    import Note from "../Timelines/Items/Note";
    import Client from "../Client/Client";
    import Ticket from "../Ticket/Ticket"
    import NotesCreate from "../Notes/Create"
    import TicketManagement from "./Management/Management"

    export default {
        name: 'TicketSummary',
        data: () => ({
            ticket: [],
            notes: [],
            notesLoading: true,
            page: 1,
            loading: true,
            note: {
                credentials: {},
            }
        }),
        components: {
            Device,
            Client,
            Timeline,
            Note,
            Ticket,
            NotesCreate,
            TicketManagement
        },
        methods: {
            fetchData() {
                this.$http.get(`/api/ticket/${this.$route.params.id}`).then((response) => {
                    this.ticket = response.data.data.ticket;
                    this.loading = false;
                    this.loadDefault();
                });

                this.getTicketNotes(1);
            },
            getTicketNotes(page) {
                this.$http.get(`/api/ticket/${this.$route.params.id}/notes?page=${page}`).then((response) => {
                    this.notes = response.data.data.notes;
                    this.notesLoading = false;
                });
            },
            async addNote() {
                this.$http.post('/api/note', this.note.credentials)
                    .then(() => {
                        this.$toasted.show('Utworzono notatk', {
                            type: 'success'
                        });
                    })
            },
            loadDefault() {
                this.note.credentials.ticket_id = this.ticket.id
            },
        },
        created() {
            this.fetchData();
        },
        computed: {
            notesLength() {
                if (this.notes.data) {
                    return this.notes.data.length;
                } else {
                    return 0;
                }
            },
        },
        watch: {
            page: function (value) {
                this.getTicketNotes(value);
            },
        },
    }
</script>

