<template>
    <v-container fluid>
        <v-row>
            <v-col class="col-12">
                <v-card class="ma-3" :elevation="5">
                    <v-card-title>
                        Informacje o kliencie: {{ client.name }}

                        <v-spacer></v-spacer>

                        <client-management :client="client"/>

                    </v-card-title>
                </v-card>
            </v-col>

            <v-col class="col-12 col-sm-12 col-md-6 col-lg-4 col-xl-4">
                <client :client="client" :loading="loading"></client>
            </v-col>

            <v-col class="col-12 col-sm-12 col-md-6 col-lg-8 col-xl-8">
                <timeline v-if="ticketsLength" :loading="ticketsLoading" title="Zgoszenia" :itemsCount="ticketsLength">
                    <ticket v-for="ticket in tickets.data" :key="ticket.id" :ticket="ticket"></ticket>

                    <template v-slot:pagination>
                        <v-pagination
                                v-model="page"
                                :length="tickets.last_page"
                                total-visible="7"
                                circle
                        ></v-pagination>
                    </template>
                </timeline>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import Timeline from "../Timelines/Timeline";
    import Ticket from "../Timelines/Items/Ticket"
    import Client from "./Client";
    import ClientManagement from "./Management/Management";

    export default {
        name: 'ClientSummary',
        data: () => ({
            client: [],
            tickets: [],
            loading: true,
            ticketsLoading: true,
            page: 1,
        }),
        components: {
            Timeline,
            Ticket,
            Client,
            ClientManagement
        },
        methods: {
            async fetchClient() {
                this.$http.get(`/api/client/${this.$route.params.id}`).then((response) => {
                    this.client = response.data.data.client;
                    this.loading = false;
                });

                this.getClientTickets(1);
            },
            getClientTickets(page) {
                this.$http.get(`/api/client/${this.$route.params.id}/tickets?page=${page}`).then((response) => {
                    this.tickets = response.data.data.tickets;
                    this.ticketsLoading = false;
                });
            },
        },
        created() {
            this.fetchClient()
        },
        computed: {
            ticketsLength() {
                if (this.tickets.data) {
                    return this.tickets.data.length;
                } else {
                    return 0;
                }
            },
        },
        watch: {
            page: function (value) {
                this.getClientTickets(value);
            },
        },
    }
</script>

